.topnav {
  overflow: hidden;
  background-color: #006E84;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 999; }
  .topnav ul {
    list-style: none;
    border: none;
    padding: 0;
    margin: 0; }
    .topnav ul li {
      list-style: none;
      border: none;
      padding: 0;
      margin: 0; }
      .topnav ul li a {
        float: left;
        display: block;
        color: #f2f2f2;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 17px; }
        .topnav ul li a:hover {
          background-color: #007D80;
          color: white; }
  .topnav .icon {
    display: none; }

.active {
  background-color: #424242;
  color: #f2f2f2 !important; }

.dropdown {
  float: left;
  overflow: hidden; }

@media screen and (max-width: 1110px) {
  .topnav ul li:not(:first-child) {
    display: none; }
  .topnav ul li:last-child {
    float: right;
    display: block; }
    .topnav ul li:last-child a.icon {
      float: right;
      display: block; }
  .topnav.responsive {
    position: relative;
    display: block; }
    .topnav.responsive .icon {
      position: absolute;
      right: 0;
      top: 0; }
    .topnav.responsive ul, .topnav.responsive li, .topnav.responsive a {
      float: none;
      display: block !important;
      text-align: left; } }

